  },
  watch: {
    currentAction(newValue) {
      this.initLogs();
    },
    from(newValue) {
      if (this.to.length > 0) {
        let ymdFrom = newValue.replace(/_/g, "/")
        let ymdTo = this.to.replace(/_/g, "/")
        if (new Date(ymdFrom).getTime() > new Date(ymdTo).getTime()) {
          this.from = this.to
        }
      }
    },
    to(newValue) {
      if (this.from.length > 0) {
        let ymdFrom = this.from.replace(/_/g, "/")
        let ymdTo = newValue.replace(/_/g, "/")
        if (new Date(ymdFrom).getTime() > new Date(ymdTo).getTime()) {
          this.to = this.from
        }
      }
    }
  },
  created() {
    this.tableHeight = document.documentElement.clientHeight - 220;
    this.from = this.to = this.$tools.formatYYMMDD(new Date().getTime())
    this.initLogs();
  },
  methods: {
    initLogs() {
      this.displayLogs = [];
      if (this.actions.indexOf(this.currentAction) == 0) {
        this.displayLogs = this.logs;
      } else {
        this.logs.some((item) => {
          if (item.action == this.currentAction) {
            this.displayLogs.push(item);
          }
        });
      }
    },
    shortId(id) {
      let front = id.substring(0, 2);
      let back = id.substring(id.length - 2, id.length);
      return front + "..." + back;
    },
    onCopySuccess() {
      this.$notify({
        message: "Copy Success!",
        type: "success",
      });
    },
    onCopyError() {
      this.$notify({
        message: "Copy Failed!",
        type: "error",
      });
    },
